import React, { Component } from 'react';
import { Switch, Route } from 'react-router-dom';
import asyncImport from './components/AsyncImport';

const _404 = asyncImport(() => import( './pages/404'));
const Index = asyncImport(() => import( './pages/Index'));
const Login = asyncImport(() => import( './pages/Login'));
const Site = asyncImport(() => import( './components/forms/Site'));
const SiteDetail = asyncImport(() => import( './pages/SiteDetail'));
const SiteDetailForm = asyncImport(() => import( './components/forms/SiteEntry'));
const CreateUserForm = asyncImport(() => import( './components/forms/CreateUser'));

class Router extends Component {
    render() {
        return (
            <Switch {...this.props}>
                <Route exact path="/" component={Index} {...this.props}/>
                <Route exact path="/login" component={Login} {...this.props}/>

                {/* the question mark (?) at the end is for optional arguments */}
                <Route exact path="/site" component={Site} {...this.props}/>
                <Route exact path="/site/:edit?/:id?" component={Site} {...this.props}/>
                
                <Route exact path="/siteDetail/:id" component={SiteDetail} {...this.props}/>                
                <Route exact path="/makeEntry/:siteId/:edit?/:id?" component={SiteDetailForm} {...this.props}/>
                
                <Route exact path="/createUser" component={CreateUserForm} {...this.props}/>
                <Route component={_404} {...this.props}/>
            </Switch>
        );
    }
}

export default Router;



